*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #e8f0eb;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Barlow", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #34473c;
  line-height: 1.8;
}

/** Media Queries **/
.header {
  background-color: #141f1a;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 12rem;
  }
}
.header .logo {
  max-width: 10rem;
}
.header .logout {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 3rem 3rem;
  cursor: pointer;
}
.header .navegation {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column-reverse;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header .navegation {
    flex-direction: row;
    margin-top: 0;
  }
}
.header .menu-nav {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header .menu-nav {
    display: flex;
    flex-direction: row;
  }
}
.header .menu-nav a {
  padding: 1rem;
  font-size: 2rem;
  color: #e8f0eb;
}
.header .menu-nav a:hover {
  color: #82a691;
}

.main {
  background-image: url(../img/background-header.webp);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  lazyload: true;
  height: 65rem;
  padding: 5rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main {
    flex-direction: row;
    padding: 5rem 0;
  }
}
.main .information {
  padding: 1rem;
  max-width: 100%;
}
.main .information .title {
  background: linear-gradient(to right, #adc8b8 0%, white 100%);
  color: transparent;
  background-clip: text;
  font-size: 4rem;
  font-weight: 900;
  text-align: center;
}
.main .information .message {
  color: #e8f0eb;
  font-weight: 400;
  font-size: 2rem;
  padding: 0 1rem;
}
.main .information .message span {
  background: linear-gradient(to right, #adc8b8 0%, white 100%);
  color: transparent;
  background-clip: text;
  font-weight: 900;
}
.main .redirect {
  display: flex;
  justify-content: center;
  align-items: center;
}
.main .redirect .button {
  background-color: #141f1a;
  padding: 2.5rem 6rem;
  color: #e8f0eb;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 2rem 4rem;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .main .redirect .button {
    width: auto;
  }
}
.main .redirect .button:hover {
  background-color: #283e34;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main .information {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 50%;
  }
  .main .information .title {
    font-size: 5rem;
  }
  .main .information .message {
    font-size: 2.5rem;
    padding: 0 3.5rem;
  }
  .main .redirect {
    width: 50%;
  }
  .main .redirect .button {
    background-color: #141f1a;
    padding: 2.5rem 6rem;
    color: #e8f0eb;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: 0.3s;
    text-align: center;
    display: block;
    width: 100%;
    font-size: 2.5rem;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .main .redirect .button {
    width: auto;
  }
}
@media (min-width: 768px) {
  .main .redirect .button:hover {
    background-color: #283e34;
    cursor: pointer;
  }
}

.contenedor {
  text-align: center;
}
.contenedor h2 {
  font-size: 3rem;
  font-weight: 400;
  color: #141f1a;
  margin: 3rem 0;
}

.container-adds {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .container-adds {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.add {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 1.25rem;
  border: 0.0625rem solid #ccc;
  border-radius: 0.5rem;
  background: linear-gradient(145deg, #5f8870, #3d5848);
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .add {
    margin: 0;
  }
}
.add__img {
  width: 100%;
  max-width: 18rem;
  border-radius: 0.5rem;
  margin-bottom: 0.625rem;
}
.add__name {
  font-size: 2.5rem;
  font-weight: 900;
  color: #e8f0eb;
  margin: 1rem 0;
}
.add__description {
  font-size: 1.8rem;
  margin: 1rem 0;
  color: #e8f0eb;
  margin-bottom: 1.25rem;
}
.add__button {
  display: inline-block;
  padding: 2rem 3rem;
  background-color: #141f1a;
  color: #e8f0eb;
  text-decoration: none;
  border-radius: 0.3125rem;
  transition: background-color 0.3s;
}
.add__button:hover {
  background-color: #34473c;
}